import React from "react";
import ButtonIconComponent from "../../shared-components/buttons/ButtonIcon.component";
import IconCloseComponent from "../../shared-components/icons/IconClose.component";
import useToastrStore from "../../store/hooks/use-toastr-store.hook";
import ToastrType from "../models/toastr.type";

const ToastrItem = ({
  variant,
  toastrItem,
}: {
  toastrItem: ToastrType;
  variant?: "danger" | "warning" | "success" | "primary";
}) => {
  const baseCssClass = "shadow-md rounded p-3 flex items-center justify-between";
  const { removeToastr } = useToastrStore();

  const handleClose = () => {
    removeToastr(toastrItem.id);
  };

  React.useEffect(() => {
    const timeoutId = setTimeout(() => {
      removeToastr(toastrItem.id);
    }, 3000);

    return () => {
      clearTimeout(timeoutId);
    };
  }, [removeToastr, toastrItem]);

  return (
    <li className={getCssClassNameByVariant(variant, baseCssClass)}>
      <span>{toastrItem.message}</span>
      <ButtonIconComponent variant={variant} onClick={handleClose}>
        <IconCloseComponent className="w-3 h-3" />
      </ButtonIconComponent>
    </li>
  );
};

export default React.memo(ToastrItem);

const getCssClassNameByVariant = (variant: "danger" | "warning" | "success" | "primary", baseClassName: string) => {
  const baseClass = `${baseClassName} bg-white`;
  const variantClasses = {
    danger: `${baseClassName} bg-red-300 text-red-900`,
    warning: `${baseClassName} bg-orange-300 text-orange-900`,
    success: `${baseClassName} bg-emerald-500 text-white`,
    primary: `${baseClassName} bg-sky-500 text-white`,
  };

  return variant ? variantClasses[variant] : baseClass;
};
