import React from "react";
import ToastrItemComponent from "./ToastrItem.component";
import useToastrStore from "../../store/hooks/use-toastr-store.hook";

const Toastr = () => {
  const { toastrList } = useToastrStore();
  return (
    <ul className="flex flex-col gap-2 fixed left-2 bottom-2 w-64">
      {toastrList.map((toastr) => (
        <ToastrItemComponent key={toastr.id} toastrItem={toastr} variant={toastr.variant} />
      ))}
    </ul>
  );
};

export default React.memo(Toastr);
