import AppStore from "..";
import ToastrType from "../../toastr/models/toastr.type";
import ToastrStore from "../slices/toastr-store.slice";

const useToastrStore = () => {
  const dispatch = AppStore.useAppDispatch();
  const selectToastrList = AppStore.useAppSelector(ToastrStore.select.getToastrList);

  const dispatchAddToastr = (toastr: ToastrType) => {
    dispatch(ToastrStore.actions.add(toastr));
  };

  const dispatchRemoveToastr = (id: string) => {
    dispatch(ToastrStore.actions.remove(id));
  };

  return {
    toastrList: selectToastrList,
    addToastr: dispatchAddToastr,
    removeToastr: dispatchRemoveToastr,
  };
};

export default useToastrStore;
