import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import ToastrType from "../../toastr/models/toastr.type";
import AppStore from "..";
import getUUID from "../../helpers/get-uuid.helper";
namespace ToastrStore {
  type ToastrStoreType = {
    toastrList: ToastrType[];
  };
  const initialState: ToastrStoreType = {
    toastrList: [],
  };
  const toastrStoreSlice = createSlice({
    initialState,
    name: "toastr",
    reducers: {
      add: (state, action: PayloadAction<ToastrType>) => {
        const toastrId = getUUID();
        state.toastrList.push({ ...action.payload, id: toastrId });
      },
      remove: (state, action: PayloadAction<string>) => {
        state.toastrList = state.toastrList.filter((toastr) => toastr.id !== action.payload);
      },
    },
  });
  export const actions = toastrStoreSlice.actions;
  export const reducer = toastrStoreSlice.reducer;
  export const select = {
    getToastrList: (state: AppStore.RootState) => state.toastr.toastrList,
  };
}

export default ToastrStore;
